
.topbar{
    width: 100%;
    height: 50px;
    background-color: #ffff;
    position: -webkit-sticky;
    position: sticky;
    top: 0%;
    z-index: 999;
}

.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    font-weight: bold;
    font-style: 33px;
    color: darkblue;
    cursor: pointer;
}
.topbarRight{
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-right: 10px;
    padding: 5px;
    color: #555;
}
.topbarIconsContainer{
    position: relative;
}
.topbarIconsBadges{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0px;
    background-color: red;
    color: aliceblue;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;

}
.topAvtar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}
.sidebar{
    flex: 1 1;
    height: calc(100vh -50px);
    background-color: rgb(243, 243, 243);
    position: -webkit-sticky;
    position: sticky;
    top: 60px;

}
.sidebarWrapper{
    padding: 20px;
    color: #555;
}
.sidebarMenu{
    margin-bottom: 10px;
}
.sidebarTittle{
    font-size: 13px;
    color: rgb(190, 186, 186);
}
.sidebarList{
    list-style: none;
    padding: 5px;
}
.sidebarListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}
.sidebarListItem.active, .sidebarListItem:hover{
     background-color: rgb(215, 217, 223); ;
}
.sidebarIcon{
    margin-right: 5px;
    font-size: 20px !important;
}

.container{
    display: flex;
    margin-top: 10px;
}

.Feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.featureItem{
    flex: 1 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 3px 5px 2.5px rgba(0,0,0,0.75);
}
.featureTittle{
    font-size: 20px;
}
.featureMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}
.featureMoney{
    font-size: 30px;
    font-weight: 600;
}
.featureMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
    font-weight: bold;
}
.featureIcons{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}
.featureIcons.negative{
    color: red;
}
.featureSub{
    font-size: 15px;
    color: gray;
}
.home{
    flex: 4 1;
}
.homeWidgets{
    display: flex;
    margin: 20px;
}


.Chart{
margin: 20px;
padding: 20px;
box-shadow: 0px 3px 5px 2.5px rgba(0,0,0,0.75);
}
.chartTittle{
    margin-bottom: 20px;
}
.files1{
    flex: 1 1;
    padding: 20px;
    margin-right: 20px;
    box-shadow: 0px 3px 5px 2.5px rgba(58, 58, 58, 0.75);
}
.wetgettitle{
    font-size: 30px;
    font-weight: 600;
}
.wetgetimg{
    width: 42px;
    height: 42px;
    border-radius: 50%;
    object-fit: cover;
}
.wetgetlist{
    margin: 0;
    padding: 0;
    list-style: none;
}
.wetgetitem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;
}
.wetgetuser{
    display: flex;
    flex-direction: column;
}
.wetgedusername{
    font-weight: 600;
}
.wetgetusertitle{
    font-weight: 300;
}
.wetgetButton{
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;

}
.wetgetIcons{
    font-size: 16px !important;
    margin: 5px;
}
.files2{
    flex: 2 1;
    padding: 20px;
    box-shadow: 0px 3px 5px 2.5px rgba(66, 66, 66, 0.75);
}
.wegdetlgTitle{
    font-size: 22px;
    font-weight: 600;
}

.wegdetlgtable{
    width: 100%;
    border-spacing: 20px;
}

.wegdetlgth{
    text-align: left;
}
.wegdetlgUser{
    display: flex;
    align-items: center;
    font-weight: 600;
}

.wegdetlgimg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.wegdetlgButton{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;

}
.wegdetlgButton.Approved{
    background-color: #e5faf2;
    color: rgb(4, 173, 4);

}
.wegdetlgdate, .wegdetlgamount{
    font-weight: 300;
}

.wegdetlgButton.Declined{
    background-color: #fff0f1;
    color: rgb(187, 5, 5);
    
}
.wegdetlgButton.Pending{
    background-color: #ebf1eb;
    color: rgb(7, 7, 175);
    
}

.UserList{
    flex: 4 1;
}
